import React from "react"
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Layout from "../layouts/fullheight"
import SEO from "../components/seo"
import {SpringLink} from "../components/navigation/springLink"
import Footer from "../components/footer"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faLongArrowAltRight} from '@fortawesome/free-solid-svg-icons'

const About = () => {
    const data = useStaticQuery(graphql`
    query {
      noelImage: file(relativePath: { eq: "noel.jpg" }) {
        childImageSharp {
          fluid(
            quality: 100
          ) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
  return(
  <Layout>
    <SEO title="About Noel" />
    <Container className='my-5 text-light d-flex flex-column flex-grow-1 justify-content-center'>
        <Row className="justify-content-center align-items-center">
          <Col xs={{span:6}} sm={{span:4}}>
            <div style={{maxWidth:'300px'}} className="mx-auto">
              <Img fluid={data.noelImage.childImageSharp.fluid} fadeIn={true} className='mb-3 rounded-circle'/>
            </div>
          </Col>
          <Col xs={{span:12}} sm={{span:6}}>
            <div className="px-3 body-bg">
              <h1 className="mb-0 py-3">Hey, Google! Welcome.</h1>
              <p>Here are some quick links to my resume, cover letter and portfolio. Enjoy!</p>
              <div className="d-flex flex-row">
                <div className="p2 mr-2 d-block flex-fill">
                  <a href="https://drive.google.com/open?id=1qwPB7lS2dWdJ890X1D-Y0hmlv677CFto" target="_blank" rel="noopener noreferrer">
                    <div className="d-block"><span className="btn text-light btn-lg border-secondary border w-100">Cover Letter &amp; Resume</span></div>
                  </a> 
                </div>
              </div>
              <div className="mt-3" style={{fontSize:'1.25em'}}>
                <SpringLink to="/" className="">
                  <div className="d-block btn btn-lg btn-success">
                      View my portfolio <FontAwesomeIcon icon={faLongArrowAltRight}/>
                  </div>
                </SpringLink>
              </div>
            </div>
          </Col>
        </Row>
    </Container>
    <Footer />
  </Layout>
  )
}

export default About
