/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React,{Component} from "react"
import PropTypes from "prop-types"
import SEO from '../../components/seo'
import { Spring } from 'react-spring/renderprops'
import { TransitionState } from 'gatsby-plugin-transition-link'

import "bootstrap/dist/css/bootstrap.css"


class FullHeightLayout extends Component {

  constructor(props) {
    super(props);
    this.state = {
      checkIsHomePage: this.props.page ? true : false
    };
  }

  render(){
    return (
      <>
        <SEO title="Noel Torres Portfolio"/>
        <TransitionState>
          {({ transitionStatus }) => {
            const mount = [ 'entering','entered'].includes(transitionStatus)
            const exitStatus = [ 'exiting','exited'].includes(transitionStatus)
            const transitionStatusPop = transitionStatus === 'POP' ? true : false
            return (
              <Spring
                from={{
                  opacity: mount ? 0 : 0,
                  transform: `translateY(${exitStatus ? '10px' : 0})`
                }}
                to={{
                  opacity: mount || transitionStatusPop ? 1 : 0,
                  transform: `translateY(${mount ? 0 : '10px'})`
                }}
              >
                {props =>
                <>
                  <main className={['d-flex','flex-column','vh-100'].join(' ')} style={props} >
                    {this.props.children}
                  </main>
                </>
              }
              </Spring>
            )
          }}
        </TransitionState>
        
      </>
    )
  }
}

FullHeightLayout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default FullHeightLayout
